import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
//自定义组件
import chatInput from '@/components/chatinput.vue';
import messageShow from '@/components/messageshow.vue';
export default {
	components: {
		uniNavBar,
		chatInput,
		messageShow
	},
	data() {
		return {
			style: {
				pageHeight: 0,
				contentViewHeight: 0,
				footViewHeight: 90,
				mitemHeight: 0,
			},
			scrollTop: 0,
			//显示的数据
			messages: [{
				user: 'home',
				type: 'head',
				content: 'hello!!!!!!'
			}],
			pageH: 0
		};
	},
	onLoad:function(){
		console.log(uni.getSystemInfoSync().windowHeight);
		this.pageH = uni.getSystemInfoSync().windowHeight;
		
	},
	methods: {
		getInputMessage: function(message) {
			console.log(message);
			// 将数据展示在页面上
			// 将数据push进messages的列表中
			this.addMessage("customer", message.content, false);
			console.log(this.messages.length);
			var MessagePageLen = this.pageH-44-47;
			var MessageNum = parseInt(MessagePageLen/100);
			console.log("MM"+MessageNum+","+MessagePageLen);
		},
		addMessage: function(user, content, hasSub, subcontent) {
			var that = this;
			that.messages.push({
				user: user,
				content: content,
				hasSub: hasSub,
				subcontent: subcontent
			});
		},
		backpage: function() {
			console.log("返回页面");
			uni.navigateBack();
		},
		scroll: function(e) {
			this.scrollTop = e.detail.scrollTop;
			console.log(this.scrollTop);
		}

	}
}
